import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { Space, Button, Drawer, Tag, Divider } from 'antd'
import navigationService from '../../service/navigation'
import { LocalStoreKey, getSetting } from '../../service/settings'
import store from '../../store'
import type { DicDto } from '../../models/dto/DicDto'
import { exportDic, runBuild } from '../../api/express'
import { ADMIN_MODE } from '../../api/common'
import { InstagramOutlined } from '@ant-design/icons'
import { RoutePath } from '../../service/router'
import InfoIcon from '../common/InfoIcon'
import MenuButtonLink from './MenuButtonLink'
import { menuEventManager } from './menuEventManager'

const Menu = (): JSX.Element => {
  const navigate = useNavigate()
  const [openSideMenu, setOpenSideMenu] = useState(false)

  useEffect(() => {
    navigationService.setNavigation(navigate)
  }, [])
  const logoutHandler = (): void => {
    localStorage.removeItem(LocalStoreKey.TOKEN)
    location.reload()
  }

  const isAuth: boolean = getSetting(LocalStoreKey.TOKEN, '') !== ''

  const exportHandler = (): void => {
    const dic: DicDto = store.getState().dic
    if (dic.tags.length !== 0 && dic.types.length !== 0 && dic.words.length !== 0) {
      exportDic(dic)
    } else {
      console.error('    .')
    }
  }

  const buildHandler = (): void => {
    runBuild()
  }

  const handlerEffector = (): void => {

  }

  const hideMenu = (): void => {
    setOpenSideMenu(false)
  }

  menuEventManager.onClose(hideMenu)

  return (
    <>
      <Drawer title={''}
              open={openSideMenu}
              onClose={hideMenu}
              placement={'left'}
              closable={true}
      >
        <Space direction={'vertical'} size={2}>
          <Space direction={'horizontal'}>
            <MenuButtonLink to={RoutePath.HOME} label={''}/>
            <MenuButtonLink to={RoutePath.NUMBERS} label={''}/>
          </Space>
          <Space direction={'horizontal'}>
            <MenuButtonLink to={RoutePath.AFFIXES} label={''}/>
            <MenuButtonLink to={RoutePath.PHRASES} label={''}/>
          </Space>
          <Divider orientation={'left'}> ..</Divider>
          <MenuButtonLink to={RoutePath.INTRODUCTORY_PHONETIC} label={'- '}/>
          <MenuButtonLink to={RoutePath.BASIC} label={' '}/>
          <MenuButtonLink to={RoutePath.PHONETICS} label={'- '} hide/>

          <Divider orientation={'left'}> </Divider>
          <MenuButtonLink to={RoutePath.QUESTION_TABLE} label={' '}/>
          <MenuButtonLink to={RoutePath.LOCATIVE_TABLE} label={' '}/>
          <MenuButtonLink to={RoutePath.BELONGING_SINGLE_TABLE} label={'  '}/>
          <MenuButtonLink to={RoutePath.GENITIVE_TABLE} label={' '}/>
          <MenuButtonLink to={RoutePath.MULTIPLICITY_TABLE} label={' '}/>
          <MenuButtonLink to={RoutePath.DATIVE_TABLE} label={' '}/>

          <Divider orientation={'left'}></Divider>
          <MenuButtonLink to={RoutePath.HYMN} label={' '}/>
          {ADMIN_MODE && <MenuButtonLink to={RoutePath.SERVICE} label={''}/>}
        </Space>
      </Drawer>
      <Space>
        <Button type={'primary'} onClick={() => {
          setOpenSideMenu(true)
        }}></Button>
        <Tag icon={<InstagramOutlined/>} color="#55acee">
          <a href={'https://www.instagram.com/vadmark_in_kyrgyzstan/'} target={'_blank'}
             rel={'noopener noreferrer'}></a><br/>
        </Tag>
        <InfoIcon tooltip={' : ..  (  )'}/>
        {ADMIN_MODE && <>
          {!isAuth ? <Link key={'4'} to={'/register'}></Link> : null}
          {!isAuth ? <Link key={'5'} to={'/login'}></Link> : null}
          {isAuth ? <Button onClick={logoutHandler}></Button> : null}
            <Button type={'primary'} onClick={exportHandler}> </Button>
            <Button type={'primary'} onClick={buildHandler}></Button>
            <Link to={'effector'}>
                <Button type={'primary'} onClick={handlerEffector}>Effector</Button>
            </Link>
        </>}
      </Space>
    </>
  )
}

export default Menu
